:root{
  --transition-color: rgb(133, 129, 85);
}


.game-board {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}
.roll-appear{
  opacity:0;
}
.roll-enter{
  opacity:0;
}
.roll-enter-done{
  opacity:1;
  transition: opacity 1500ms;
}

.character-cards {
  margin: 2em 10px;
}
.character-cards:hover{
  filter: brightness(2);
  transition: filter 300ms;
  /* Just for fun
   transform: rotate(90deg);
  transition: transform 3s; */
}

img {
  width: 15vw;
  height: 33vh;
}

@media  screen and (max-width: 800px) {
  img {
    width: 90vw;
  }
  
}